import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperPlane, faEnvelope, faPhone, faPlus } from "@fortawesome/free-solid-svg-icons";

import { useEffect, useState } from "react";
import { Container, Row, Col, Card, Button, Modal, Form } from "react-bootstrap";

import useWindowSize from '../useWindowSize.js';

import FsLightbox from "fslightbox-react";

const Quadro = (props) => {

    const size = useWindowSize();
    
    const [toggler, setToggler] = useState(false);

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleOpen = () => setShow(true);

    const handleClick = () => {
        setToggler(o => !o);
    }

    return(
        <>
        <Col className={size.width > 973 ? 'mb-4' : 'mb-4 d-flex justify-content-center'}>

        <FsLightbox toggler={toggler} sources={[props.element.src]}/>

        <Card style={{ width: '20rem'}}>
            <Card.Img onClick={handleClick} variant="top" src={props.element.src} style={ size.width > 975 ? {width: '100%', height: '12vw',objectFit:'cover', cursor: 'pointer'} : {cursor: 'pointer'}} />
            <Card.Body>
                <Card.Title className='mb-3 mt-3'><h5>{props.element.title}</h5></Card.Title>
                <Card.Text className='d-flex justify-content-center' style={{flexDirection: 'column'}}>
                    <h7 className='pb-3'>by {props.element.artist}</h7>
                    <h7 className='pb-1'>Dimensione (cm): {props.element.width} x {props.element.height}</h7>
                    <h7 className='pb-1'>Autenticità: {props.element.auth}</h7>
                    <h7 className='pb-3'>Tecnica: {props.element.tech}</h7>
                    <Button style={{backgroundColor: '#00239C'}} onClick={handleOpen} className='d-flex justify-content-center align-items-center'>
                        <h7 style={{marginRight: '10px'}}>Contatta ora</h7>
                        <FontAwesomeIcon icon={faPaperPlane} color={'white'}/> 
                    </Button>
                </Card.Text>
            </Card.Body>
        </Card>
        </Col>

        <Modal show={show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}}>
            <Modal.Header>
                <Modal.Title>Contatti</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col className='d-flex align-items-center'>
                    <FontAwesomeIcon icon={faEnvelope} color='black' />
                    <h7 className='mb-4 mt-4' style={{marginLeft:'20px'}}>Email: flashpointartshop@gmail.com</h7>
                    </Col>
                </Row>
                <Row>
                    <Col className='d-flex align-items-center'>
                    <FontAwesomeIcon icon={faPhone} color='black' />
                    <h7 className='mb-4 mt-4' style={{ marginLeft:'20px'}}>Whatsapp: +39 339 107 6327</h7>
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button>
            </Modal.Footer>
        </Modal>
        </>
    );
}

const Shop = (props) => {

    const [paintings, setPaintings] = useState([]);
    const [paintingsBK, setPaintingsBK] = useState([]);
    const [artists, setArtists] = useState([]);

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleOpen = () => setShow(true);

    useEffect(() => {

        const TEST = [
            { title: 'Quadro 1', artist: 'Artista 1', width: 80, height: 50, auth: 'Autentico', tech: 'Olio su tela', src: 'https://www.finasrl.it/wp-content/uploads/2015/12/img-test-300x225.jpg'},
            { title: 'Quadro 2', artist: 'Artista 1', width: 120, height: 100, auth: 'Autentico', tech: 'Olio su tela', src: 'https://www.finasrl.it/wp-content/uploads/2015/12/img-test-300x225.jpg'},
            { title: 'Quadro 3', artist: 'Artista 2', width: 100, height: 100, auth: 'Autentico', tech: 'Olio su tela', src: 'https://www.finasrl.it/wp-content/uploads/2015/12/img-test-300x225.jpg'},
            { title: 'Quadro 4', artist: 'Artista 3', width: 80, height: 72, auth: 'Autentico', tech: 'Olio su tela', src: 'https://www.finasrl.it/wp-content/uploads/2015/12/img-test-300x225.jpg'},
        ];

        setPaintings(TEST);
        setPaintingsBK(TEST);
        setArtists([...new Set(TEST.map(el => el.artist))].sort());

    }, []);

    const filterPaintings = (artist) => {
        setPaintings(paintingsBK.filter((el) => el.artist === artist));
    }

    const resetPaintings = () => {
        setPaintings(paintingsBK);
    }

    const submitNewQuadro = (e) => {
        e.preventDefault();

    }

    return (
        <>

        <Container fluid style={{backgroundColor: '#fff', minHeight: '60vh'}} className='pb-5 pt-5'>
            <Container>
                <Row>
                    <Col md={2} className='mb-4'>
                        <Row><h3 className='mb-4' style={{color: 'black'}}>Artisti</h3></Row>
                        <Row onClick={resetPaintings}><u><h6 className='mb-3' style={{color: 'black', cursor: 'pointer'}}>Tutti</h6></u></Row>

                        {artists.map((el, i) => <Row key={i} onClick={() => filterPaintings(el)}><u><h6 className='mb-3' style={{color: 'black', cursor: 'pointer'}}>{el}</h6></u></Row>)}

                    </Col>
                    <Col md={10}>
                        <Row>
                            <Col className='d-flex align-items-baseline'>
                            <h3 className='mb-4' style={{color: 'black'}}>Shop</h3>
                            {props.user !== null && <FontAwesomeIcon icon={faPlus} style={{marginLeft:'20px', cursor:'pointer'}} color='black' onClick={handleOpen} />}
                            </Col>
                        </Row>

                        <Row className='g-3'>
                        {paintings.map((el, i) => <Quadro key={i} element={el} />)}
                        </Row>
                    </Col>
                </Row>
            </Container>
        </Container>

        { props.user !== null &&
        <Modal show={show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}} backdrop='static'>
            
            <Form onSubmit={submitNewQuadro}>
            <Modal.Header closeButton>
                <Modal.Title>Aggiungi un quadro</Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <Form.Group className="mb-3">
                    <Form.Label>Titolo del quadro</Form.Label>
                    <Form.Control type="text" required/>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Artista</Form.Label>
                    <Form.Control type="text" required/>
                </Form.Group>
                
                <Form.Group className="mb-3">
                    <Form.Label>Dimensione (cm)</Form.Label>
                    <Row>
                        <Col><Form.Control type="number" min='1' required/></Col>
                        <Col xs={1} className='d-flex justify-content-center align-items-center'>x</Col>
                        <Col><Form.Control type="number" min='1' required/></Col>
                    </Row>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Autenticità</Form.Label>
                    <Form.Control type="text" required/>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Tecnica</Form.Label>
                    <Form.Control type="text" required/>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Foto</Form.Label>
                    <Form.Control type="file" required/>
                </Form.Group>

            </Modal.Body>

            <Modal.Footer>
                <Button style={{backgroundColor: '#00239C'}} type='submit'>Aggiungi</Button>
                <Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button>
            </Modal.Footer>
            </Form>

        </Modal>
        }
        </>
    );
}

export default Shop;